/* Navbar css */
html{
  font-size: 62.5%; /*1rem => 10px*/
}

body{
  font-size: 1.8rem;
}

nav {
  background: rgba(0, 0, 0, 0.95);
  padding: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: #fff;
  position: relative;
  z-index: 99;
}

.fas {
  cursor: pointer;
}

.navbar__menu-icon {
  display: none;
  font-size: 2rem;
}
.navbar__menu-icon .fa-times {
  -webkit-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
          transform: rotate(360deg);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.navbar__menu-icon.fa-bars {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

.navbar__redirect{
  text-decoration: none;
  cursor: pointer;
  font-size: 2.4rem;
  
}

.navbar__logo {
  font-weight: 800;
  font-size: 2.5rem;
  color: #FFB900;
}

.navbar__logo font {
  color: #fff;
}

.navbar__menu-list {
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}


.navbar__menu__item:last-child {
  font-weight: 300;
  padding: .5rem 1rem;
  background-color: var(--background-accent);
  color: white !important;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  margin-left: 2rem;
  border-radius: .2rem;
}

.navbar__menu__link.active{
  color: #FFB900;
}

.navbar__menu__item{
  margin-right: 2rem;
  padding: 0.5rem 1rem;
  text-align: center;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.navbar__menu__link {
  text-decoration: none;
  font-weight: 300;
  color: #fff;
}

.navbar__menu__item:hover {
  background-color: #fff;
  border-radius: .2rem;
  color: #000 !important;
  -webkit-transition: all 0.5s ease-in-out;
  -o-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
  cursor: pointer;
}

.navbar__menu__link__icons{
  font-size: 150%;
  width: 100%;
}

.navbar__menu__link__icons-text{
  width: 100%;
  font-size: 80%;
}

@media screen and (max-width: 80rem) {
  .navbar__logo {
    font-size: 2rem;
  }
  .navbar__menu-list li a {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 60rem) {
  .navbar__menu-icon {
    display: block;
  }
  .navbar__menu-list {
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    position: absolute;
    background: rgba(0, 0, 0, 0.7);
    padding: 2rem;
    right: 0;
    top: 6.4rem;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    width: 100%;
    display: block;
  }

  .navbar__menu-list li {
    margin-bottom: 2rem;
    width: 100%;
    text-align: center;
  }
  .navbar__menu-list li:last-child {
    margin-bottom: 0rem;
  }

  .navbar__menu-list li a {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin: auto;
    font-size: 130%;
  }

  .navbar__menu__item:last-child{
    margin-left: 0;
  }

  .navbar__menu__link__icons{
    margin-right: 2rem;
  }

  .navbar__menu-list.close {
    right: -100%;
    -webkit-transition: all 0.5s ease-in-out;
    -o-transition: all 0.5s ease-in-out;
    transition: all 0.5s ease-in-out;
    display: none;
    width: 0;
  }
}



