.loading {
	width: 100%;
	height: 100vh;
	background-color: rgba(0, 0, 0, .5);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.loading__logo {
	text-align: center;
	-ms-flex-item-align: center;
	    align-self: center;
	margin-bottom: 5rem;
	-webkit-animation: test 2s infinite;
	        animation: test 2s infinite;
}

.loading__text {
	font-size: 2rem;
}

@-webkit-keyframes test {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@keyframes test {
	0% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}

	50% {
		-webkit-transform: scale(1.2);
		        transform: scale(1.2);
	}

	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}

@media screen and (min-width: 37.5rem) {
	.loading__text {
		font-size: 4rem;
	}
}

@media screen and (min-width: 60rem) {
	.loading__text {
		font-size: 7rem;
	}
}


