.darkmode{
  position: relative;
  display: inline-block;
  width: 6.4rem;
  height: 3.4rem;
}
.darkmode input{
  opacity: 0;
  width: 0;
  height: 0;
}

.slider{
  position: absolute;
  border-radius: 1.6rem;
  cursor: pointer;

  top: 0;
  left: 0;
  right: 0;
  bottom: 0;

  background-color: var(--background-accent-secondary);
}
.slider:before{
  position: absolute;
  border-radius: 50%;
  content: "";
  height: 2.6rem;
  width: 2.6rem;
  left: 0.4rem;
  bottom: 0.4rem;
  background-color: var(--background);
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s;
}

input:checked + .slider:before{
  background-color: var(--background-secondary);
  -webkit-transform: translateX(2.8rem);
      -ms-transform: translateX(2.8rem);
          transform: translateX(2.8rem);
}



